<template>
    <!-- 头部公共部分 -->
    <div class="header_box">
        <div class="logo_box">
            <img src="../image/team.png" alt="">
        </div>
        <div class="title_tap">
            <div class="tap_box">
                <div class="text_box" @click="titleclick(1)" :class="$store.state.team==1?'title_active':''">质控品</div>
                <div class="text_box" @click="titleclick(2)" :class="$store.state.team==2?'title_active':''">第三方物流</div>
            </div>
            <div class="option_box">
                <div class="username_box">
                    <el-dropdown trigger="click">
                        <span class="el-dropdown-link">
                            王立(主管)<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <template #dropdown>
                            <el-dropdown-menu>
                            <el-dropdown-item icon="el-icon-plus">修改密码</el-dropdown-item>
                            </el-dropdown-menu>
                        </template>
                    </el-dropdown>
                </div>
                <div class="logout_box">退出登录</div>
            </div>
        </div>
    </div>
</template>

<script>
import { getCurrentInstance, reactive, toRefs } from 'vue'

export default {
    setup () {
        const { proxy } = getCurrentInstance()
        const state = reactive({
            
        })
        const titleclick = (n) => {
            if(n==1){
                proxy.$store.state.activeclass = 2
            }else{
                proxy.$store.state.activeclass = 1
            }
            proxy.$store.state.team = n
        }
        return {
            ...toRefs(state),
            titleclick
        }
    }
}
</script>

<style scoped>
img{
    width: 30px;
    height: 30px;
}
.header_box,.title_tap{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.title_tap{
    width: calc(100% - 50px);
}
.tap_box{
    display: flex;
    align-items: center;
    color: #7F7F7F;
    font-size: 18px;
}
.tap_box>div{
    margin-right: 40px;
}
.title_active{
    color: rgba(50, 150, 250, 0.996);
}
.option_box{
    display: flex;
    align-items: center;
    color: rgb(85, 85, 85);
    font-size: 14px;
    padding-right: 30px;
    box-sizing: border-box;
}
.logout_box{
    margin-left: 50px;
}
</style>