<template>
  <!-- 侧边菜单 -->
  <div>
    <div class="project_name">销售系统V1.0</div>
    <div
      class="project_menu"
      :style="{
        height: $store.state.innerheight - 92 + 'px',
        'overflow-y': 'auto',
      }"
    >
      <el-menu
        default-active="/workbench"
        class="el-menu-vertical-demo elmenu"
        background-color="#545C64"
        text-color="#fff"
        active-text-color="#3296FA"
        :router="true"
      >
        <template v-for="item in $store.state.menulist">
          <el-sub-menu
            :index="item.path"
            :key="item.path"
            v-if="
              item.children != undefined &&
              item.children != null &&
              item.children.length > 0
            "
          >
            <template #title>
              <i :class="item.iclass"></i>
              <span>{{ item.title }}</span>
            </template>
            <template v-for="imt in item.children">
              <el-sub-menu
                :index="imt.path"
                :key="imt.path"
                v-if="
                  imt.children != undefined &&
                  imt.children != null &&
                  imt.children.length > 0
                "
              >
                <template #title>
                  <i :class="imt.iclass"></i>
                  <span>{{ imt.title }}</span>
                </template>
                <el-menu-item-group v-if="imt.children.length > 0">
                  <el-menu-item
                    class="menu-item"
                    v-for="imts in imt.children"
                    :key="imts.path"
                    :index="imts.path"
                  >
                    <i :class="imts.iclass"></i>
                    <template #title>{{ imts.title }}</template>
                  </el-menu-item>
                </el-menu-item-group>
              </el-sub-menu>
              <el-menu-item :index="imt.path" v-else :key="imt.path">
                <i :class="imt.iclass"></i>
                <template #title>{{ imt.title }}</template>
              </el-menu-item>
            </template>
          </el-sub-menu>
          <el-menu-item :index="item.path" v-else :key="item.path">
            <i :class="item.iclass"></i>
            <template #title>{{ item.title }}</template>
          </el-menu-item>
        </template>
      </el-menu>
    </div>
  </div>
</template>

<script>
import { reactive } from "vue";

export default {
  setup() {
    const state = reactive({});

    return {
      state,
    };
  },
};
</script>

<style scoped>
.elmenu.el-menu {
  border: 0;
}
.el-menu-item.is-active {
  background-color: #3a3f44 !important;
}
.project_name {
  height: 90px;
  border-bottom: 1px solid;
  border-bottom-color: rgba(215, 215, 215, 1);
  font-size: 24px;
  text-align: center;
  line-height: 90px;
}
.el-sub-menu .el-menu-item,
.el-menu-item {
  height: 40px;
  line-height: 40px;
}
</style>
<style>
.el-menu-item-group__title {
  padding: 0;
}
</style>